
import logo from './logo.svg';
import './App.css';
import React, {Component} from 'react';

import { Container, Row, Col, Button,  ButtonToolbar, FormGroup, ControlLabel, FormControl } from 'react-bootstrap';

import 'bootstrap/dist/css/bootstrap.min.css';

import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';

import koshi from './koshi.xlsx';
import bhajani from './bhajani.xlsx';
import kalyanpur from './kalyanpur.xlsx';
import sarawal from './sarawal.xlsx';


// const options = [
//     'one', 'two', 'three'
//   ]


  const palikas = [
    { value: 'Sarawal Gaunpalika', label: 'Sarawal Gaunpalika' },
    { value: 'Koshi Gaunpalika', label: 'Koshi Gaunpalika' },
    { value: 'Kalyanpur Municipality', label: 'Kalyanpur Municipality' },
    { value: 'Bhajani Gaunpalika', label: 'Bhajani Gaunpalika' },
  ];


  const dates = [
    {value:2017, label:'2017'},
    {value:2018, label:'2018'},
    {value:2019, label:'2019'},
    {value:2020, label:'2020'},
  ];

  
  
  // const defaultOption = options[0];


class Data extends Component {

  state = {selectedDistrict: '', selectedDate: '',};

  constructor(props) {
    super(props);
    this.handleButtonClick = this.handleButtonClick.bind(this);
  } 

  handleChange = selectedDistrict => {
    this.setState({selectedDistrict});
    console.log("selected:", selectedDistrict.value)
  };


  handleChange2 = selectedDate => {
    this.setState({selectedDate});
    console.log("selected:", selectedDate.value)
  
  };


  handleButtonClick(){

    var palikaName = this.state.selectedDistrict.value;

    var dataYear = this.state.selectedDate.value;

    const link = document.createElement("a");

    if(palikaName=="Sarawal Gaunpalika"){
      link.href = sarawal;
      link.setAttribute(
        'download',
        `sarawal_${dataYear}.xlsx`,
      );

    }else if(palikaName=="Koshi Gaunpalika"){

      link.href = koshi;
      link.setAttribute(
        'download',
        `koshi_${dataYear}.xlsx`,
      );

    }else if(palikaName=="Kalyanpur Municipality"){
      link.href = kalyanpur;
      link.setAttribute(
        'download',
        `kalyanpur_${dataYear}.xlsx`,
      );

    }
    else if(palikaName=="Bhajani Gaunpalika"){
      link.href = bhajani;
      link.setAttribute(
        'download',
        `bhajani_${dataYear}.xlsx`,
      );

    }
    
    document.body.appendChild(link);
    link.click();

    // alert('A name was submitted: ' + palikaName+ dataYear);
  }

    render() {

      const { selectedDistrict } = this.state;
      const { selectedDate } = this.state;

      return(
          
        <div className="App">

        <Container>
        <p></p>
          <Row>
            <Col>
            <Dropdown options={palikas} value={selectedDistrict} onChange={this.handleChange} placeholder="Select Admin Division" />
            </Col>
        
            <Col>
            <Dropdown options={dates} value={selectedDate} onChange={this.handleChange2} placeholder="Select a Date" />
            </Col>
        
        
          </Row>
          <Row>
            <p></p>
          </Row>
          <Row>
            <Col></Col>
          <Button as={Col} onClick={this.handleButtonClick} variant="primary">Explore Datasets</Button>
          <Col></Col>
          </Row>


        </Container>
        
            </div>
        );
    }
  }


  export default Data;
  